const apikey = "OamihYj5o5ZcM4Apfg3ewaGUCTGWqUxG";
const axios = require("axios").default;

export const Autocomplete = async (inputValue) => {
  try {
    const response = await axios.get(
      `http://dataservice.accuweather.com/locations/v1/cities/autocomplete?apikey=${apikey}&q=${inputValue}`,
      {
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Methods": "POST, GET , DELETE , PUT , OPTIONS",
          "Access-Control-Allow-Headers": "Content-Type",
        },
        mode: "no-cors",
      }
    );

    // await axios.get(
    //   `http://dataservice.accuweather.com/locations/v1/cities/autocomplete?apikey=${apikey}&q=${inputValue}`
    // );
    return response.data;
    console.log(response.data);
  } catch (error) {
    return "error";
  }
};

export const getForecastsData = async (key) => {
  let forecastData;

  try {
    const response = await axios.get(
      `http://dataservice.accuweather.com/forecasts/v1/daily/5day/${key}?apikey=${apikey}&metric=true`
    );
    console.log(response);
    forecastData = response.data;
    console.log("getForecastsData");
    return forecastData;
    console.log(response.data);
  } catch (error) {
    return "error";
  }
};

export const getWeatherDataByKey = async (key) => {
  let results;

  try {
    const response = await axios.get(
      `http://dataservice.accuweather.com/currentconditions/v1/${key}?apikey=${apikey}`
    );
    console.log("getWeatherDataByKey");
    console.log(response);
    let forecastData = await getForecastsData(key);
    results = {
      data: response.data,
      forecasts: forecastData,
    };
    return results;
    console.log(response.data);
  } catch (error) {
    return "error";
  }
};

export const getKeyByCoords = async (latitude, longitude) => {
  let cityKey;
  let results;

  try {
    const response = await axios.get(
      `http://dataservice.accuweather.com/locations/v1/cities/geoposition/search?apikey=${apikey}&q=${latitude}%2C${longitude}&details=true`
    );
    cityKey = response.data;
    console.log("getKeyByCoords");
    console.log(response);
    results = await getWeatherDataByKey(cityKey.Key);
    results = { ...results, cityData: cityKey };
    return results;
    console.log(response.data);
  } catch (error) {
    return "error";
  }
};
